<template>
	<view>
		<view id="titleBar" class="title_bar" 
			:style="{'background':bgColor}"
			style="z-index: 998;">
			<view :style="{'height':statusBarHeight+'px'}"></view>
			<view class="viewFlexStart"
				:style='{"height":titleBottom+"px"}'>
				<slot name="titleText" ></slot>
			</view>
			<!-- 标题空出一点点 -->
			<view style="height: 10rpx;"></view>
			<view class="titleView">
				<slot name="titleView" ></slot>
			</view>
			
		</view>
		<view :style='{"padding-top":contentTop+"px"}' >
		</view>
	</view>
</template>

<script>
	export default {
		props:["bgColor"],
		data() {
			return {
				statusBarHeight:0,
				titleBottom:0,
				contentTop:0
			};
		},
		mounted() {
			let menuButtonInfo = uni.getMenuButtonBoundingClientRect();
			this.statusBarHeight=menuButtonInfo.top;
			this.titleBottom=menuButtonInfo.height;
			
			
			
			var that=this;
			that.$nextTick(()=>{
				that.queryBottom();
			});
			
		},
		methods:{
			queryBottom(){
				var that=this;
				const query = uni.createSelectorQuery().in(this);
				query.select('#titleBar').boundingClientRect(data => {
					console.log("=eiml-title-view=====data:",data);
					if(data!=null){
						that.contentTop=data.bottom;
					}
				}).exec();
			}
		}
	}
</script>

<style>
	.title_bar{
		position: fixed;
		top: 0rpx;
		left: 0rpx;
		right: 0rpx;
	}
	
	.viewFlexStart{
	    display: -webkit-flex;
	    display: flex;
	    -webkit-justify-content: flex-start;
	    justify-content: flex-start;
		align-items: center;
		
	}
</style>
